body {
  overflow-x: hidden;
}

.main-image {
  background: url('https://images.unsplash.com/photo-1509021436665-8f07dbf5bf1d?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1674&q=80');
  background-repeat: no-repeat;
  background-position:  center;
  background-size: cover;
  width: 100%;
  height: 80vh;
  background-attachment: fixed;
  position: relative;
}

.filter:after {
  background-color: rgba(0,0,0,0.4);
  content: '';
  display: block;
  height: 100%;
  left: 0;
  top: 0;
  position: absolute;
  width: 100%;
  z-index: 1;
}

.label__text {
  color: white;
  font-size: 30px;
}

.card-title {
  overflow: hidden;
  color: rgba(0,0,0,0.85);
  font-weight: 500;
  font-size: 16px;
  white-space: nowrap;
}

.card {
  margin-bottom: 20px;
}